.product-details {
  margin-top: 100px !important;
}
.prdt-img{
  box-shadow: 0 2px 4px 0 rgba(13, 12, 12, 0.6);
}
.product-details-info {
  display: flex;
  flex-direction: column;
  justify-content: center;

}

.product-details-info h6 {
  margin-bottom: 20px !important;
}

.similar-products .card {
  
  width: 18rem;
}
.btn-details{
  color:blueviolet;
  border-color: blueviolet;
}
.btn-cart{
  background-color:blueviolet;
  color: white;
}
.btn-cart .btn-details :hover {
  border-color: black;
}
.similar-products .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.similar-products .card-price {
  color: blueviolet;
  font-weight: bold;
}
.similar-products button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.similar-products .card-text {
  color: rgb(90, 89, 89);
}

.similar-products .card img:hover {
  transform: scale(0.9);
}

.similar-products .card-img-top {
  height: 300px !important;
}
