
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400&family=Ubuntu+Mono:wght@400;700&display=swap');
.home-page h1 {
  color: gray;
  font-family: 'Ubuntu Mono', monospace;
}
@media (max-width: 767px) {
  .filters {
    display: none;
  }
}
.home-page {
  display: flex;
  flex-wrap: wrap;
}
.home-page .card {
  background-color: white;
  width: 20rem;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.1); 
}
.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  font-weight: 600;
  font-size: 20px;
}

.btn-details{
  color:blueviolet;
  border-color: blueviolet;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}

.filters {
  margin-top: 5%;
  border-radius: 2px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.4); 
  margin-left: 1%;
  width:20%;
}
.head-fil{
  margin-top:2%;
  font-weight: 600;  
  color: blueviolet;
  padding: 2%;
  font-size: 20px;

}
.icons{
  margin-right: 2%;
  font-size: 17px;
}
.icons1{
  margin-right: 2%;
  font-size: 25px;

}
.filters h4 {
font-weight: 600;
font-size: 15px;  
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}
.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-image: linear-gradient(to right,blueviolet , rgb(101, 7, 115));
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
}

.loadmore {
  color: green;
  font-weight: bold;
  font-size: 20px !important;
}
